@import url(https://fonts.googleapis.com/css?family=Roboto:100&display=swap);
body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;
  /* hide scrollbar in firefox  */ }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

p {
  margin: 3px; }

h2 {
  margin-bottom: 5px;
  font-family: sans-serif; }

body {
    background: #eeeeee
}

.toggle-button {
  float: right;
  margin-right: 20px;
  width: 30px; }

.tag-input {
  width: 150px;
  font-family: 'Arial';
  font-size: 14px;
  height: 20px;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #444;
  margin-left: 10px;
  margin-top: 10px; }

.grade-table {
  border-spacing: 15px 0px; }

.student-name {
  font-size: 30px; }

.student-thumb {
  float: left; }
  .student-thumb img {
    display: block;
    margin-left: 50px;
    height: 100px; }

.student-content {
  margin-left: 200px; }

.tag-section {
  margin: 10px; }

.tag {
  padding: 8px;
  margin: 4px;
  background: #ddd;
  border-radius: 3px; }

.student-extra-info {
  margin-top: 10px; }

.filter {
  width: 97%;
  height: 35px;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #dddddd;
  font-size: 25px;
  margin-left: 10px;
  margin-top: 10px;
  color: #777777; }

.student-box {
  margin: auto;
  overflow: auto;
  width: 60%;
  height: 500px;
  background: #ffffff;
  border-radius: 10px;
  margin-top: 50px;
  z-index: 100;
  box-shadow: 1px 1px #aaaaaa; }

::-webkit-scrollbar {
  width: 0px;
  /* Remove scrollbar space */
  /* Optional: just make scrollbar invisible */ }

.header {
  position: fixed;
  background: white;
  width: 60%; }

.content {
  padding-top: 80px; }

